import React, { useState } from "react";
import { useSpring, animated } from "react-spring";
import { ReactComponent as Logo } from "./assets/art/svg/logo/Catalyst-Logo_group.svg";

export default function Sun()  {

  const [resetSun, setResetSun] = useState(false);

  const propsSun = useSpring({
    from: { transform: "rotate(0deg)" },
    to: { transform: "rotate(360deg)" },
    onRest: () => setResetSun(state => !state),
    config: { duration: 10000 },
    reset: resetSun
  });

  return (
      <animated.div style={propsSun}><Logo /></animated.div>
  );
};